row:first-child span:first-child,\n.nav-header:first-child span:first-child,\n.tag-header:first-child span:first-child {\n\twidth: 100%;\n\theight: 2px;\n\tbackground-color: rgb(87, 111, 114);\n}\n\n.user-row:first-child span:nth-child(3),\n.nav-header:first-child span:nth-child(3),\n.tag-header:first-child span:nth-child(3) {\n\twidth: 100%;\n\theight: 2px;\n\tbackground-color: rgb(87, 111, 114);\n}\n\n.user-details .user-row:nth-child(1),\n.nav-buttons .nav-header:nth-child(1),\n.tags > .tag-header:nth-child(1) {\n\tfont-size: 1.2rem;\n\tfont-weight: 600;\n\tfont-family: \"Montserrat\", \"Poppins\", var(--default-fonts);\n\ttext-transform: capitalize;\n}\n\n.user-row {\n\tdisplay: flex;\n\talign-items: center;\n\tgap: 16px;\n}\n\n.user-row .user-picture {\n\tborder-radius: 50%;\n}\n\n.user-row .notifications {\n\tmargin-left: auto;\n}\n\n.nav-buttons {\n\tdisplay: flex;\n\tflex-direction: column;\n\tjustify-content: space-evenly;\n\tgap: 16px;\n}\n\n.nav-buttons .nav-row,\n.nav-buttons .nav-header {\n\tdisplay: flex;\n\talign-items: center;\n\tgap: 16px;\n}\n\n.nav-row > p {\n\tcursor: pointer;\n\ttransition: transform 0.2s;\n}\n\n.nav-row > p:hover {\n\ttransform: translateY(-1px);\n}\n\n.tags {\n\tdisplay: flex;\n\tflex-direction: column;\n\tjustify-content: space-evenly;\n\tgap: 16px;\n}\n\n.tags .tag-row,\n.tags .tag-header {\n\tdisplay: flex;\n\talign-items: center;\n\tgap: 16px;\n}\n\n.tag-row p {\n\tcursor: pointer;\n\tmargin-right: auto;\n\ttransition: transform 0.2s;\n}\n\n.tag-row p:hover {\n\ttransform: translateY(-1px);\n}\n\n.tags > button {\n\tborder: 1px solid rgb(87, 111, 114);\n\tborder-radius: 5px;\n\toutline: none;\n\t/* background-color: rgb(87, 111, 114, 0); */\n\tbackground: none;\n\tcursor: pointer;\n\ttransition: 0.5s;\n\tfont-size: 1.2rem;\n\tcolor: rgba(87, 111, 114, 0.5);\n}\n\n.tags > button:hover,\n.tags > button:active {\n\tcolor: rgba(87, 111, 114, 1);\n}\n\n#content {\n\tgrid-column: 2 / 3;\n\tpadding: 48px;\n\tbackground-color: rgb(240, 235, 227);\n}\n\n.header > .header-details > h2 {\n\tfont-size: 1.6rem;\n\tfont-family: \"Montserrat\", \"Poppins\", var(--default-fonts);\n}\n\n.header {\n\tdisplay: flex;\n}\n\n.header-details {\n\tdisplay: flex;\n\talign-items: center;\n\tgap: 16px;\n}\n\n.header-details > .material-symbols-outlined {\n\tfont-size: 2rem;\n}\n\n.header-buttons {\n\tdisplay: flex;\n\talign-items: center;\n\tmargin-left: auto;\n}\n\n.cards {\n\tpadding: 48px;\n\tdisplay: grid;\n\tgrid-template-columns: 1fr;\n\tgap: 32px;\n}\n\n.card {\n\theight: 5vh;\n\tbackground-color: rgb(228, 220, 207);\n\tborder-radius: 5px;\n\tborder-left: 10px solid rgb(87, 111, 114);\n\tdisplay: flex;\n\talign-items: center;\n\tgap: 16px;\n\tpadding: 16px;\n\tcolor: rgb(87, 111, 114);\n}\n\n.empty-card {\n\theight: 5vh;\n\tbackground-color: rgb(228, 220, 207);\n\tborder-radius: 5px;\n\tborder-left: 10px solid rgb(87, 111, 114);\n\tdisplay: flex;\n\talign-items: center;\n\tgap: 16px;\n\tpadding: 16px;\n}\n\n.card-info {\n\tdisplay: grid;\n\tgrid-auto-flow: column;\n\talign-items: center;\n}\n\n.card-title {\n\ttext-transform: lowercase;\n\ttransform: translateY(-1px);\n\tfont-size: 1.3rem;\n\tfont-weight: 500;\n\tfont-family: \"Montserrat\", \"Poppins\", var(--default-fonts);\n\talign-self: center;\n}\n\n.card-tags {\n\ttext-transform: lowercase;\n\tfont-weight: 300;\n}\n\n.card .card-buttons {\n\tdisplay: flex;\n\talign-items: center;\n\tgap: 16px;\n\tmargin-left: auto;\n}\n\n.empty-card {\n\talign-items: center;\n\tjustify-content: center;\n\tcursor: pointer;\n}\n\n.empty-card span {\n\tcolor: rgb(87, 111, 114);\n\tfont-size: 2.5rem;\n\topacity: 0.5;\n\ttransition: all 0.5s;\n}\n\n.empty-card:hover > span {\n\ttransform: translateY(0px) rotate(0deg);\n\topacity: 1;\n}\n\ndialog[open] {\n\tdisplay: flex;\n\tjustify-content: center;\n\talign-items: center;\n\theight: 100vh;\n\tmax-height: 100vh;\n\twidth: 100%;\n\tmax-width: 100vw;\n\tbackground: none;\n\toverflow: hidden;\n}\n\ndialog[open]::after {\n\tcontent: \"\";\n\theight: 100vh;\n\twidth: 100%;\n\tbackdrop-filter: blur(10px);\n\tposition: absolute;\n\tz-index: 0;\n}\n\n.wrapper {\n\tz-index: 1;\n\tpadding: 64px;\n\tbackground-color: rgba(228, 220, 207, 1);\n\tborder-radius: 5px;\n\tbox-shadow: 0 0 20px rgba(0, 0, 0, 0.5);\n\tposition: relative;\n}\n\n.dialog-form {\n\tdisplay: flex;\n\tflex-direction: column;\n\tgap: 24px;\n}\n\n.dialog-form .form-close {\n\tposition: absolute;\n\tright: 0;\n\ttop: 0;\n\tmargin: 12px;\n\tpadding: 0 8px;\n\toutline: none;\n\tborder: 2px solid rgb(87, 111, 114);\n\tfont-size: 1.5rem;\n\tbackground: none;\n\talign-self: end;\n\tborder-radius: 5px;\n\ttransition: 0.2s;\n}\n\n.form-close::after {\n\tcontent: \"\\00d7\";\n}\n\n.form-close:hover {\n\tbackground-color: rgb(87, 111, 114);\n\tcolor: rgb(240, 235, 227);\n}\n\n.dialog-form input:not([type=\"checkbox\"]),\n.dialog-form select,\n.dialog-form textarea {\n\tbackground: rgb(240, 235, 227);\n\toutline: none;\n\tborder: none;\n\tborder-radius: 5px;\n\tbox-shadow: 0 0 20px rgba(0, 0, 0, 0.1);\n\tpadding: 2px 8px;\n\tfont-size: 0.9rem;\n\ttransition: color 0.2s;\n}\n\n.dialog-form input:not([type=\"checkbox\"]),\n.dialog-form textarea,\n.dialog-form select {\n\tcolor: rgba(0, 0, 0, 0.5);\n}\n\n.dialog-form input:first-of-type::placeholder {\n\tcolor: rgb(255, 101, 119);\n\ttext-align: end;\n\ttransition: 0.5s;\n}\n\n.dialog-form input:not([type=\"checkbox\"]):hover,\n.dialog-form input:not([type=\"checkbox\"]):focus,\n.dialog-form textarea:hover,\n.dialog-form textarea:focus,\n.dialog-form select:hover,\n.dialog-form select:focus {\n\tcolor: rgb(0, 0, 0);\n}\n\n.dialog-form textarea {\n\twidth: 300px;\n\theight: 150px;\n\tresize: none;\n}\n\n.dialog-form label:not(.checkbox) {\n\tdisplay: flex;\n\talign-items: center;\n\tfont-size: 1.2rem;\n\tfont-family: \"Montserrat\", \"Poppins\", var(--default-fonts);\n\tgap: 12px;\n}\n\n.dialog-form label:not(.checkbox) span:first-child {\n\twidth: 100%;\n\theight: 2px;\n\tbackground-color: rgb(87, 111, 114);\n}\n\n.dialog-form label:not(.checkbox) span:last-child {\n\twidth: 100%;\n\theight: 2px;\n\tbackground-color: rgb(87, 111, 114);\n}\n\nlabel.checkbox {\n\tposition: relative;\n\tmargin: 5px 0;\n\tcursor: pointer;\n}\n\nlabel.checkbox input {\n\tappearance: none;\n\tdisplay: none;\n}\n\nlabel.checkbox span {\n\tposition: relative;\n\tdisplay: block;\n\twidth: 40px;\n\theight: 25px;\n\tbackground: rgb(228, 220, 207);\n\tborder-radius: 40px;\n\tbox-shadow: inset 0 2px 15px rgba(0, 0, 0, 0.2);\n}\n\nlabel.checkbox .indicator {\n\tposition: absolute;\n\ttop: 0;\n\tleft: 0;\n\twidth: 25px;\n\theight: 25px;\n\tbackground: linear-gradient(\n\t\tto bottom,\n\t\trgb(125, 157, 156),\n\t\trgb(87, 111, 114)\n\t);\n\tborder-radius: 50%;\n\tbox-shadow: 0 2px 5px rgba(0, 0, 0, 0.5),\n\t\tinset 0 1px 1px rgba(255, 255, 255, 0.2);\n\ttransition: 0.5s;\n}\n\nlabel.checkbox input:checked ~ .indicator {\n\tleft: 15px;\n}\n\nlabel.checkbox .indicator::before {\n\tcontent: \"\";\n\tposition: absolute;\n\ttop: 50%;\n\tleft: 50%;\n\ttransform: translate(-50%, -50%);\n\twidth: 3px;\n\theight: 3px;\n\tbackground: rgb(255, 0, 0);\n\tborder-radius: 50%;\n\ttransition: 0.5s;\n}\n\nlabel.checkbox input:checked ~ .indicator::before {\n\tbackground: rgb(0, 255, 0);\n}\n\n.dialog-form .form-button {\n\tborder: 2px solid rgb(87, 111, 114);\n\tborder-radius: 5px;\n\toutline: none;\n\tbackground-color: rgb(87, 111, 114, 0);\n\tcursor: pointer;\n\ttransition: 0.2s;\n}\n.dialog-form > .form-button:hover,\n.tags > .form-button:active {\n\tbackground-color: rgb(87, 111, 114);\n\tcolor: rgb(240, 235, 227);\n}\n\n.dialog-form input[type=\"color\"] {\n\talign-self: center;\n}\n"],"sourceRoot":""}]);
